/*********************
General
*********************/

html {
	font-size: 62.5%; // 10px
	height: 100%;
}

body {
	color: #fff;
	font-size: 1.4rem;
	line-height: $lineheight;
	font-family: $haptik;
	height: 100%;
	/*background: $black;*/
	background-color: #fff;
	@include font-smoothing(on);
	overflow-y: scroll;
}

::-moz-selection {
	color: $white;
	background: #141414;
	-webkit-text-fill-color: $white;
	text-fill-color: $white;
}

::selection {
	color: $white;
	background: #141414;
	-webkit-text-fill-color: $white;
	text-fill-color: $white;
}


/*********************
Links
*********************/

a, a:visited {
	color: #f62425;
	text-decoration: none;
	transition: color 500ms $easeOutQuad;
	-webkit-tap-highlight-color : rgba(25,25,25,0);

	&:hover {
		transition: color 0;
		color: $link-hover;
	}

	&:focus {
		outline: none;
	}

	&:active {
		color: $link-hover;
	}
}


/*********************
Headings
*********************/

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5 {
	text-rendering: optimizelegibility;
}


/*********************
Icons
*********************/

.icon {
	display: inline-block;
}


/*********************
Container
*********************/

#container {
	& > div {
		opacity: 0;
	}
}

#video-overlay {
	opacity: 0;
	z-index: 9999;
	position: fixed;
	top: 0; bottom: 0;
	left: 0; right: 0;
	display: none;
	width: 100%;
	height: 100%;
	background: #141414;

	svg {
		position: absolute;
		top: 0; bottom: 0;
		left: 0; right: 0;
		display: block;
		margin: auto;
	}
}