#movie {
	height: 100%;
	width: 100%;

	.container {
		height: 100%;

		article {
			position: relative;
			height: 100%;
			width: span(12);
			max-width: 1280px;
			margin: 0 auto;
		}

		.movie {
			&__item__wrapper {
				overflow: hidden;
				position: absolute;
				top: 50%;
				height: 0;
				width: 100%;
				padding-bottom: 56.25%;
				transform: translateY(-50%);
				background: #000;
			}

			&__item {
				.text-wrapper {
					z-index: 15;
					color: #fff;
					line-height: 1rem;
					text-align: center;
					cursor: pointer;
					user-select: none;
					position: absolute;
					top: 53%;
					left: 50%;
					display: block;
					width: 100%;
					margin: 0 auto;
					padding: 0 20px;
					box-sizing: border-box;
					transform: translate(-50%, -50%) scale(2);

					&.animated {
						transform: translate(-50%, -50%) scale(1);
						transition: opacity 0.1s ease;
					}

					&.hidden {
						opacity: 0;
						visibility: hidden;
					}

					h1 {
						color: #fcdeb0;
						font-size: 6rem;
						line-height: 7rem;
						white-space: normal;
						letter-spacing: 13px;
						font-family: $haptik;
						text-transform: uppercase;
						margin-bottom: 25px;
						transition: color 0.4s ease;
					}

					.info {
						display: block;
						overflow: hidden;
						height: 13px;

						i {
							opacity: 0.6;
							color: #fff;
							font-size: 1.3rem;
							line-height: 1.3rem;
							letter-spacing: 7px;
							text-transform: uppercase;
							font-family: $akzidenz;
							display: block;
							transform-origin: center;

							&.director {
								transition: opacity 0.4s linear 0.35s, transform 0.5s $easeOutQuint 0s;
							}

							&.hover-watch {
								opacity: 1;
								transition: transform 0.3s $easeOutQuint 0s;
							}
						}
					}

					&:hover, &.clicked {
						h1 {
							color: #fff !important;
						}

						i {
							opacity: 1;

							&.director {
								transform: translateY(-100%);
								transition: opacity 0.3s linear 0s, transform 0.3s $easeOutQuint;
							}

							&.hover-watch {
								transform: translateY(-100%);
								transition: transform 0.5s $easeOutQuint 0s;
							}
						}
					}
				}

				.poster {
					z-index: 5;
					position: relative;
					height: 100%;
					width: 100%;
					transition: all 0.3s ease;

					&.hidden {
						opacity: 0;
						visibility: hidden;
					}
				}

				.canvas-loader {
					z-index: 5;
					user-select: none;
					position: absolute;
					left: 0; right: 0;
					bottom: 60px;
					display: block;
					height: 20px;
					width: 20px;
					margin: 0 auto;
				}

				.video-full-wrapper {
					z-index: 10;
					position: absolute;
					top: 0; bottom: 0;
					left: 0; right: 0;
					display: block;
					width: 100%;
					height: 100%;
					transition: all 0.1s ease;

					&.hidden {
						opacity: 0;
						visibility: hidden;
					}

					.mobile-cover, .icon--press-play { display: none; }

					.video-info {
						color: #fff;
						text-align: left;
						font-size: 1.5rem;
						line-height: 2.5rem;
						letter-spacing: 2px;
						font-family: $akzidenz;
						z-index: 15;
						position: absolute;
						top: 10%;
						left: 7%;

						span {
							overflow: hidden;
							display: block;
							height: 25px;

							i {
								display: inline-block;
							}

							&:first-of-type {
								i {
									transition: 1s all cubic-bezier(.97,.12,.06,.88) 0s;
								}
							}

							&:last-of-type {
								i {
									transition: 1s all cubic-bezier(.97,.12,.06,.88) 0.1s;
								}
							}
						}

						&.hidden {
							span {
								i {
									opacity: 0;
									visibility: hidden;
									transform: translate3d(0, 100%, 0);
								}

								&:first-of-type {
									i {
										transition: 0.6s all cubic-bezier(.97,.12,.06,.88) 1.1s;
									}
								}

								&:last-of-type {
									i {
										transition: 0.6s all cubic-bezier(.97,.12,.06,.88) 1s;
									}
								}
							}
						}

						.video-director {
							display: block;
						}

						a {
							color: #fff;
							position: relative;

							&::after {
								content: '';
								position: absolute;
								left: -1px;
								bottom: -1px;
								display: block;
								height: 1px;
								width: 100%;
								background: #fff;
							}
						}
					}

					video, iframe, object {
						position: absolute;
						top: 0; bottom: 0;
						left: 0; right: 0;
						display: block;
						height: auto;
						width: 100%;
						margin: auto;
					}

					iframe {
						height: 100%;
						background: #141414;
					}
				}
			}
		}
	}
}

