// MOBILE
body.mobile, body.tablet {
	#movie {
		.container {
			height: 100vh;

			article {
				width: 100%;
			}

			.movie {
				line-height: 1;
				overflow: hidden;
				left: 0; right: 0;

				&__item {
					overflow: hidden;
					height: 100%;
					width: 100%;
					margin: 0;

					&__wrapper {
						position: relative;
						top: 0; bottom: 0;
						height: 100%;
						padding-bottom: 0;
						transform: none;
					}

					.text-wrapper {
						z-index: 15;
						pointer-events: none;
						transform: translate3d(-50%, -50%, 0);

						h1 {
							margin-bottom: 35px;
						}

						.info {
							overflow: visible;
							height: auto;

							i {
								font-size: 1.5rem;
								line-height: 1.5rem;
								font-family: $akzidenz;

								&.hover-watch { display: none; }
							}
						}

						&:hover, &:active, &.clicked {
							h1 {
								color: #fff !important;
							}

							i {
								&.director, &.hover-watch {
									transform: none;
								}
							}
						}
					}

					.video-full-wrapper {
						z-index: 10;

						&.is-playing {
							height: 0;
							padding-bottom: 56.25%;
							top: 50%;
							transform: translateY(-50%);

							.controls { bottom: -55px; }

							@media (orientation: landscape) {
								.controls { top: none; bottom: 0; }
							}
						}

						video, iframe {
							z-index: -1;
						}

						.mobile-cover {
							z-index: 6;
							position: absolute;
							top: 0; left: 0;
							pointer-events: none;
							display: block;
							height: 100%;
							width: 100%;
							transition: opacity 0.2s ease;

							&.hidden {
								opacity: 0;
								visibility: hidden;
							}
						}

						.icon--press-play {
							z-index: 1;
							position: absolute;
							top: 0; bottom: 0;
							left: 0; right: 0;
							display: block;
							margin: auto;
							background-position: 0 100%;
							width: 75px;
							height: 75px;
							transition: all 0.2s ease;

							&.hidden {
								opacity: 0;
							}
						}
					}
				}
			}
		}
	}

	@media screen and (max-width: 680px) {
		#movie {
			.container {
				.movie {
					&__item {
						.text-wrapper {
							h1 {
								margin-bottom: 20px;
							}

							.info {
								i {
									font-size: 1.1rem;
									line-height: 1.1rem;
								}
							}
						}
					}
				}
			}
		}
	}

	@media screen and (max-width: 555px) {
		#movie {
			.container {
				.movie {
					&__item {
						.text-wrapper {
							padding: 0 10px;

							h1 {
								margin-bottom: 10px;
							}

							.info {
								i {
									font-size: 0.75rem;
									line-height: 0.75rem;
									letter-spacing: 3px;
								}
							}
						}
					}
				}
			}
		}
	}

	@media screen and (max-width: 355px) {
		#movie {
			.container {
				.movie__item .text-wrapper .info i {
					font-size: 0.7rem;
					line-height: 0.7rem;
					letter-spacing: 2px;
				}
			}
		}
	}
}