#about-us {
	font-family: $akzidenz;
	text-align: center;
	width: 100%;
	min-height: 100%;
	padding-top: 130px;

	.center {
		position: absolute;
		top: 50%;
		left: 0; right: 0;
		max-width: 1600px;
		margin: 0 auto;
		transform: translateY(-50%);
	}

	.container.no-title {
		height: calc(100vh - 130px);

		.center {
			position: absolute;
			top: 50%;
			left: 0; right: 0;
			transform: translateY(-50%);
		}

		.social-link {
			padding-bottom: 0;
			margin-top: 80px;
		}

		@media screen and (max-height: 650px) {
			.contact {
				margin-top: 50px;

				&__item {
					margin-bottom: 50px;
				}
			}
		}
	}

	.head {
		color: #fff;
		font-size: 9rem;
		line-height: 11rem;
		font-family: $haptik;
		padding: 0 40px;

		del {
			color: #000;
			text-decoration: none;
			background: #141414;
		}

		p:first-of-type {
			max-width: 1250px;
			margin: 0 auto 100px auto;
		}

		p:last-of-type {
			max-width: 1200px;
			margin: 0 auto 25px auto;
			color: #f62425;
		}
	}

