	.address, .phone {
		color: #f62425;
		font-size: 1.3rem;
		line-height: 3.5rem;
		letter-spacing: 8px;
		text-transform: uppercase;
		display: block;
		transition: color 0.3s ease;

		&:hover {
			color: rgba(246,36,37,0.7);
		}
	}

	.contact {
		font-size: 0;
		/*margin-top: 80px;*/


		&__item {
			color: #f62425;
			font-size: 1.3rem;
			letter-spacing: 5px;
			line-height: 2.3rem;
			white-space: nowrap;
			text-transform: uppercase;
			display: inline-block;
			vertical-align: top;
			width: span(4);
			margin: 0 span(0.5) 80px span(0.5);

			span, a {
				display: block;
			}

			.name {
				margin-bottom: 9px;
			}

			.mail, .role {
				font-size: 1.2rem;
			}

			.role {
				color: #f62425;
			}

			.mail {
				color: #f62425;
				transition: color 0.3s ease;

				&:hover {
					color: rgba(246,36,37,0.7);
				}
			}
		}
	}

	.social-link {
		padding-bottom: 150px;

		&__item {
			display: inline-block;
			vertical-align: middle;
			margin: 0 60px;

			a {
				display: block;
				padding: 10px 15px;

				&:hover {
					.icon {
						opacity: 0.7;
					}

					.text {
						&::after {
							transform: scaleX(1);
							transform-origin: center left;
						}
					}
				}

				.icon {
					transition: opacity 0.2s ease;
				}

				.text {
					color: #f62425;
					font-size: 1.4rem;
					line-height: 1.4rem;
					letter-spacing: 2px;
					font-family: $akzidenz;
					text-transform: lowercase;
					position: relative;

					&::after {
						content: '';
						position: absolute;
						left: -2px; right: 0;
						bottom: -2px;
						width: 98%;
						height: 1px;
						margin: auto;
						background: rgba(246,36,37,1);
						transform: scaleX(0);
						transform-origin: center right;
						transition: transform 0.2s $easeOutQuad;
					}
				}
			}
		}
	}
}

// MEDIA QUERIES
@media screen and (max-width: 1440px) {
	#about-us {
		.contact {
			&__item {
				font-size: 1.2rem;
				letter-spacing: 4px;
			}
		}
	}
}

@media screen and (max-width: 1300px) {
	#about-us {
		.contact {
			&__item {
				font-size: 1.1rem;
				letter-spacing: 3px;
			}
		}
	}
}

@media screen and (max-width: 1090px) {
	#about-us {
		.head {
			font-size: 7rem;
			line-height: 8.5rem;
		}

		.contact {
			&__item {
				font-size: 1.3rem;
				letter-spacing: 5px;
				width: 50%;
				margin: 0 auto 80px auto;
			}
		}
	}
}

@media screen and (max-width: 1000px) {
	#about-us {
		.container.no-title {
			height: auto;

			.center {
				position: static;
				top: auto;
				transform: none;
				padding: 45px 20px 50px 20px;
				box-sizing: border-box;
			}

			.social-link {
				padding-bottom: 0px;
			}
		}
	}
}
@media screen and (max-width: 770px) {
	#about-us {
		.head {
			font-size: 6rem;
			line-height: 7rem;
		}

		.social-link {
			padding-bottom: 100px;
		}
	}
}

@media screen and (max-width: 700px) {
	#about-us {
		.contact__item {
			width: 100%;
		}
	}
}

@media screen and (max-width: 600px) {
	#about-us {
		padding-top: 70px;

		.head {
			font-size: 4rem;
			line-height: 5rem;

			p:first-of-type {
				margin-bottom: 50px;
			}
		}

		.contact {
			margin-top: 50px;

			&__item {
				margin-bottom: 50px;
			}
		}

		.social-link {
			padding-bottom: 25px;

			&__item {
				margin: 0 3%;
			}
		}
	}
}

@media screen and (max-width: 500px) {
	#about-us {
		padding-top: 100px;

		.head {
			font-size: 3.5rem;
			line-height: 4.5rem;
			margin-bottom: 50px;
			margin-top: 20px;

			p:first-of-type {
				margin-bottom: 20px;
			}

		}

		.address, .phone {
			font-size: 1rem;
			line-height: 2.5rem;
			letter-spacing: 4px;
		}

		.contact {
			margin-top: 75px;
			margin-bottom: 40px;

			&__item {
				margin-bottom: 25px;

				.name, .role, a {
					font-size: 1rem;
					line-height: 2.5rem;
					letter-spacing: 4px;
				}

				.name {
					margin-bottom: 0;
				}
			}
		}
	}
}

@media screen and (max-width: 400px) {
	#about-us {
		padding-top: 70px;

		.head {
			font-size: 3rem;
			line-height: 4rem;
			margin-bottom: 50px;
		}

		.contact {
			margin-top: 55px;
			margin-bottom: 30px;
		}
	}
}

body.mobile, body.tablet {
	#about-us {
		/*overflow: hidden;*/
		height: 100vh;
		width: 100%;
		padding-top: 0;

		&::before {
			content: '';
			z-index: 1;
			pointer-events: none;
			left: 0; right: 0;
			display: block;
			width: 100%;
			height: 110px;
			background: #fff;
			position: fixed;
		}

		.contact{
			margin: 100px 0 !important;
		}
	}
}