.directors, .directors-category {
	width: 100%;
	height: 100%;
	text-align: center;

	&__cover {
		position: absolute;
		z-index: 1;
		width: 100%;
		left: 0;
		top: 0;
		opacity: 0;
	}

	&__background {
		width: 100%;
		height: 100%;
		display: inline-block;
		position: absolute;
		top: 0;
		left: 0;
		line-height: 0;

		.scale {
			$p: 13 / 18;
			position: absolute;
			width: 100%;
			height: 100%;
			overflow: hidden;
			transform-origin: 0 0;

			&::after {
				content: '';
				display: block;
				height: 0;
				width: 100%;
				padding-bottom: calc(56.25% - 4px);
			}
		}

		ul {
			position: absolute;
			width: 100%;
			display: block;
			font-size: 0;
		}

		li {
			position: absolute;
			/*display: inline-block;*/
			width: 100%;
			height: 100vh;
			opacity: 0;

			&::after {
				content: '';
				display: block;
				height: 0;
				padding-bottom: 56.25%;
			}

			.wrapper {
				/*opacity: 0.8;*/
				position: absolute;
				left: 0;
				width: 100%;
				height: 100%;
			}

			img {
				-o-object-fit: cover;
				object-fit: cover;
				display: block;
				height: 100%;
				width: 100%;
			}
		}
	}

	nav {
		display: flex;
		position: absolute;
		/*top: 50%;
		left: 0;*/
		width: 100%;
		height: 100%;
		align-items: center;
		z-index: 9999;

		ul {
			position: absolute;
			width: 100%;
			max-width: 1024px;
			left: 0;
    		right: 0;
    		margin: 0 auto;
			line-height: 5.5rem;
			column-count: 3;
			/*padding: 0 10%;*/

			li {
				position: relative;
				white-space: nowrap;
				display: block;
				height: 40px;

				&.active {
					transition: none;

					a {
						transition: none;
						opacity: 1;
					}
				}

				a {
					/*pointer-events: auto !important;
					opacity: 0.25;
					color: #f62425;
					// font-size: 1.6rem;
					// font-size: 9rem;
					line-height: 0;
					position: absolute;
					top: 0; bottom: 0;
					left: 0; right: 0;
					display: inline-block;
					height: 0;
					margin: auto;
					$scale: 1.6 / 9;
					transform: scale($scale);*/
					color: #fff;
					text-transform: uppercase;
				    font-size: 1.4rem !important;
				    letter-spacing: 0.2rem !important;
					transition: opacity 0.4s $easeOutQuad;

					&:hover {
						opacity: 1;
					}
				}

				a:hover{
					/*color: #f62425;*/
					color: #fff;
					opacity: 0.7;
				}
			}
		}
	}

	&_filters{
		position: absolute;
		bottom: 20px;
		z-index: 999999;
		width: 100%;

		li{
			cursor: pointer;
			display: inline-block;

			a{
			color: #f62425;
			margin-right: 15px;
					font-size: 1.4rem;
					line-height: 1.4rem;
					letter-spacing: 2px;
					font-family: $akzidenz;
					text-transform: lowercase;
					position: relative;
					display: inline-block;
					padding: 20px 0;
					transition: color 0.2s ease;
					&::after {
						content: '';
						position: absolute;
						left: -2px; right: 0;
						bottom: 17px;
						width: 98%;
						height: 1px;
						margin: auto;
						background: rgba(246,36,37,1);
						transform: scaleX(0);
						transform-origin: center right;
						transition: transform 0.2s $easeOutQuad;
					}

					&:hover {
						color: #f62425;

						&::after {
							transform: scaleX(1);
							transform-origin: center left;
						}
					}
				}

					&:last-of-type {
					margin-right: 0px;
				}

				&:not(:first-of-type) {
					&::before {
						content: '';
						display: inline-block;
						vertical-align: middle;
						height: 1px;
						width: 1px;
						margin: 0 15px 0 0;
						background: #f62425;
					}
				}
		}
	}
}

// MOBILE
body.mobile, body.tablet {
	.directors {
		overflow: hidden;
		height: 100vh;
		width: 100%;

		&.animated {
			&::before {
				display: none;
			}
		}

		&::before {
			content: '';
			z-index: 1;
			pointer-events: none;
			position: absolute;
			left: 0; right: 0;
			display: block;
			width: 100%;
			height: 110px;
			background: #fff;
			position: fixed;
		}

		nav {
			z-index: 0;
			ul {
				li {
					a {
						color: #f62425;
						letter-spacing: 7px;
						font-family: $haptik;
						text-transform: uppercase;
						margin: 0;

						&:hover {
							opacity: 0.2;
						}
					}
				}
			}
		}

		&__cover {
			opacity: 1;
			visibility: hidden;
		}

		&__background {
			.scale {
				position: fixed;
				top: 110px;
				left: 50%;
				width: 90%;
				height: 87vh;
				height: calc(100% - 135px);
				transform: translate(-50%, 0%);
			}

			ul {
				height: 100%;

				li {
					position: absolute;
					top: 0; left: 0;
					height: 100%;

					.wrapper {
						height: 100%;

						.cover-blur {
							opacity: 0;
							visibility: hidden;
						}

						img {
							position: absolute;
							top: 0; left: 0;
							height: 100%;
							width: auto;
							transform: translateX(-50%);

							@supports (object-fit: cover) {
								object-fit: cover;
								height: 100%;
								width: 100%;
								transform: none;
							}
						}

						.img {
							position: absolute;
							top: 50%;
							left: 50%;
							height: 100vh;
							width: 100vh;
							margin-top: -43px;
							transform: translate(-50%, -50%);
						}
					}
				}
			}
		}
	}

	@media screen and (max-width: 555px) {
		.directors {
			&::before {
				height: 85px;
			}

			nav {
				align-items: baseline;
				ul {
					column-count: 1;
					margin: 100px 0;
					li {
						a {
							letter-spacing: 4px;
						}
					}
				}
			}

			&__background {
				.scale {
					top: 85px;
					height: 93vh;
					height: calc(100% - 105px);
				}

				ul {
					li {
						.wrapper {
							.img {
								margin-top: -35px;
							}
						}
					}
				}
			}
		}
	}

	@media screen and (max-width: 350px) {
		.directors {
			&__background {
				.scale {
					width: 95%;
					height: calc(100% - 95px);
				}

				ul {
					li {
						.wrapper {
							.img {
								margin-top: -38px;
							}
						}
					}
				}
			}
		}
	}
}