.video-component-wrapper {
	line-height: 1;
	position: relative;

	video {
		cursor: auto;
	}

	.controls {
		line-height: 55px;
		vertical-align: middle;
		z-index: 5;
		position: absolute;
		left: 0; right: 0;
		bottom: 0px;
		display: block;
		height: 55px;
		width: 60%;
		max-width: 640px;
		margin: 0 auto;
		transition: all 0.3s ease;

		&.hidden {
			opacity: 0;
			visibility: hidden;
		}

		&__toggle {
			cursor: pointer;
			display: inline-block;
			height: 55px;
			width: 50px;
			border: none;
			background: none;

			&--play {
				outline: none;
				position: absolute;
				left: 0; top: 0;

				&::before {
					content: '';
					position: absolute;
					top: 0; bottom: 0;
					left: 0; right: 0;
					display: block;
					height: 10px;
					width: 10px;
					margin: auto;
					background: url('data:image/svg+xml;dataset=utf-8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%225%22%20height%3D%228%22%20viewBox%3D%220%200%205%208%22%3E%3Cpath%20fill%3D%22%23ffffff%22%20fill-rule%3D%22nonzero%22%20d%3D%22M0%200l5%204-5%204v-8zm0%200h1v8h-1v-8zm1%201h1v6h-1v-6zm1%201h1v4h-1v-4zm1%201h1v2h-1v-2z%22/%3E%3C/svg%3E') center no-repeat;
					background-size: contain;
				}
				
				&.pause {
					&::before {
						background: url('data:image/svg+xml;dataset=utf-8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%229%22%20height%3D%228%22%20viewBox%3D%220%200%209%208%22%3E%3Cpath%20fill-rule%3D%22evenodd%22%20fill%3D%22%23f9f9f9%22%20d%3D%22M0%200h3v8h-3v-8zm6%200h3v8h-3v-8z%22/%3E%3C/svg%3E') center no-repeat;
						background-size: contain;
					}
				}

			}

			&--fullscreen {
				outline: none;
				position: absolute;
				right: 0; top: 0;

				&::before {
					content: '';
					position: absolute;
					top: 0; bottom: 0;
					left: 0; right: 0;
					display: block;
					height: 4px;
					width: 8px;
					margin: auto;
					border: 2px solid #fff;
				}
			}
		}

		&__bar {
			cursor: pointer;
			position: absolute;
			top: 0; bottom: 0;
			left: 0; right: 0;
			display: block;
			height: 4px;
			width: 80%;
			max-width: 525px;
			margin: auto;
			border: 2px solid #fff;

			&--buffer {
				cursor: pointer;
				position: absolute;
				top: 0; 
				left: 0;
				left: -0.5px; 
				right: 0;
				display: block;
				width: 100%;
				height: 100%;
				background: rgba(255,255,255,0.3);
				transform-origin: left center;
				transform: scaleX(0);
			}

			&--progress {
				position: absolute;
				top: 0;
				left: 0;
				left: -0.5px; 
				right: 0;
				display: block;
				width: 100%;
				height: 100%;
				background: #fff;
				transform-origin: left center;
				transform: scaleX(0);
			}
		}
	}
}

// MEDIA QUERIES
@media screen and (max-width: 900px) {
	.video-component-wrapper {
		.controls {
			width: 100%;

			&__bar {
				width: 70%;
				width: calc(100% - 100px);
			}
		}
	}
}