/*****************************************
Grid
*****************************************/

$susy: (
	flow: ltr,
	math: fluid,
	output: float,
	gutter-position: after,
	container: auto,
	container-position: center,
	columns: $columns,
	gutters: $gutter,
	column-width: false,
	global-box-sizing: content-box,
	last-flow: to,
	// debug: (
	// 	image: show,
	// 	color: rgba(#00FF00, .30),
	// 	output: overlay,//background,
	// 	toggle: top right
	// ),
	use-custom: (
		background-image: true,  
		background-options: false,
		// box-sizing: true,
		// clearfix: false,
		// rem: true,
	)
);

head:hover ~ .container:before, head:hover ~ body .container:before {
	background-size: calc(#{100 / $columns}% + 1px), 100% 20px !important;
}

.container {
	@include container(1600px);
}