@font-face {
    font-family: 'akzidenz-grotesk_regular';
    src: url('fonts/akzidenzgrotesk-regular.eot');
    src: url('fonts/akzidenzgrotesk-regular.eot?#iefix') format('embedded-opentype'),
         url('fonts/akzidenzgrotesk-regular.woff2') format('woff2'),
         url('fonts/akzidenzgrotesk-regular.woff') format('woff'),
         url('fonts/akzidenzgrotesk-regular.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'gt_haptik_medium';
    src: url('fonts/gt-haptik-medium.eot');
    src: url('fonts/gt-haptik-medium.eot?#iefix') format('embedded-opentype'),
         url('fonts/gt-haptik-medium.woff2') format('woff2'),
         url('fonts/gt-haptik-medium.woff') format('woff'),
         url('fonts/gt-haptik-medium.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}

$haptik: "gt_haptik_medium", "Helvetica Neue", Helvetica, Arial, sans-serif;
$akzidenz: "akzidenz-grotesk_regular", "Helvetica Neue", Helvetica, Arial, sans-serif;