/*****************************************
Header
*****************************************/

#header {
	z-index: 100;
	line-height: 120px;
	/*overflow: hidden;*/
	position: fixed;
	height: 0;
	width: 100%;
	height: 85px;
	padding: 0 2rem;
	box-sizing: border-box;

	.nav {
		height: 100%;
		line-height: 120px;

		&__logo {
			color: transparent;
			font-size: 2.8rem;
			line-height: 0px;
			text-transform: uppercase;
			display: inline-block;
			vertical-align: middle;
			margin-left: 40px;
		}

		&__topright {
			/*float: right;*/
			/*margin-right: 40px;*/
			position: absolute;
    		top: 0;
    		right: 0;

    		&__item {
				display: inline-block;
				vertical-align: middle;
				margin-right: 15px;

				a {
					color: #f62425;
					font-size: 1.4rem;
					line-height: 1.4rem;
					letter-spacing: 2px;
					font-family: $akzidenz;
					text-transform: lowercase;
					position: relative;
					display: inline-block;
					padding: 20px 0;
					transition: color 0.2s ease;

					&::after {
						content: '';
						position: absolute;
						left: -2px; right: 0;
						bottom: 17px;
						width: 98%;
						height: 1px;
						margin: auto;
						background: rgba(246,36,37,1);
						transform: scaleX(0);
						transform-origin: center right;
						transition: transform 0.2s $easeOutQuad;
					}

					&:hover {
						color: #f62425;

						&::after {
							transform: scaleX(1);
							transform-origin: center left;
						}
					}
				}

				&:last-of-type {
					margin-right: 0px;
				}

				&:not(:first-of-type) {
					&::before {
						content: '';
						display: inline-block;
						vertical-align: middle;
						height: 1px;
						width: 1px;
						margin: 0 15px 0 0;
						background: #f62425;
					}
				}
			}
		}

		&__top {
			float: right;
			margin-right: 40px;
			/*position: absolute;
    		top: 0;
    		left: 50%;
    		transform: translate3d(-50%,0,0);*/


			.submenu{
				position: absolute;
				top: 76px;
				opacity: 0;
				pointer-events: none;

				li{
					line-height: initial;
					a{
						padding: 10px 0 !important;

						&::after {
							content: '' !important;
							bottom: 7px !important;
						}
					}
				}
			}

			#menu-item-2745:hover{
				.submenu{
					opacity: 1;
					pointer-events: all;
				}
			}

			&__item {
				display: inline-block;
				vertical-align: middle;
				margin-right: 15px;

				a {
					color: #f62425;
					font-size: 1.4rem;
					line-height: 1.4rem;
					letter-spacing: 2px;
					font-family: $akzidenz;
					text-transform: lowercase;
					position: relative;
					display: inline-block;
					padding: 20px 0;
					transition: color 0.2s ease;

					&::after {
						content: '';
						position: absolute;
						left: -2px; right: 0;
						bottom: 17px;
						width: 98%;
						height: 1px;
						margin: auto;
						background: rgba(246,36,37,1);
						transform: scaleX(0);
						transform-origin: center right;
						transition: transform 0.2s $easeOutQuad;
					}

					&:hover {
						color: #f62425;

						&::after {
							transform: scaleX(1);
							transform-origin: center left;
						}
					}
				}

				&:last-of-type {
					margin-right: 0px;
				}

				&:not(:first-of-type) {
					&::before {
						content: '';
						display: inline-block;
						vertical-align: middle;
						height: 1px;
						width: 1px;
						margin: 0 15px 0 0;
						background: #f62425;
					}
				}
			}
		}
	}

	.close-poppin {
		opacity: 0;
		visibility: hidden;
		color: #fdfdfd;
		font-size: 1.4rem;
		line-height: 120px;
		letter-spacing: 3px;
		font-family: $akzidenz;
		text-transform: lowercase;
		position: absolute;
		top: 0; bottom: 0;
		right: 40px;
		display: inline-block;
		padding: 0 2rem;
		transition: color 0.2s ease;

		&::after {
			content: '';
			position: absolute;
			left: -3px; right: 0;
			bottom: 5px;
			width: 51%;
			height: 1px;
			margin: auto;
			background: #fff;
			transform: scaleX(0);
			transform-origin: center right;
			transition: transform 0.2s $easeOutQuad;
		}

		&:hover {
			color: #fff;

			&::after {
				transform: scaleX(1);
				transform-origin: center left;
			}
		}
	}
}

// TABLET
body.tablet {
	#header {
		transition: all 0.5s ease 0.3s;

		&.hidden {
			opacity: 0;
			visibility: hidden;
		}
	}
}

// MOBILE
body.mobile {
	#header {
		height: 90px;
		transition: all 0.5s ease 0.3s;

		&.hidden {
			opacity: 0;
			visibility: hidden;

			.nav__logo, .nav__top__item {
				transform: translateY(-15px);
			}
		}

		.nav {
			line-height: 80px;
			text-align: center;

			&__logo {
				margin-left: 0;
				transform: translateY(0px);
				transition: transform 0.5s ease 0.1s;
			}

			&__top {
				float: none;
				margin-right: 0;
				margin-top: -40px;

				position: relative;
				left: 0;
				transform: translate3d(0, 0, 0);

				&__item {
					transform: translateY(0px);
					transition: transform 0.5s ease 0.2s;

					&:not(:first-of-type) {
						&::before { display: none; }
					}
				}
			}
		}

		.close-poppin {
			top: 21px;
			left: 0; right: 0;
			margin: auto;

			&::after { display: none; }
		}
	}

	@media screen and (max-width: 555px) {
		#header {
			.nav {
				line-height: 65px;

				&__logo {
					svg {
						width: 100px;
					}
				}

				&__top {
					margin-top: -25px;

					&__item {
						a {
							font-size: 1.2rem;

							&::after { display: none; }

							&:hover { color: #f62425; }
						}
					}
				}
			}

			.close-poppin {
				font-size: 1.2rem;
				top: -1px;
			}
		}
	}
}