#latest {
	min-height: 100vh;
}

.latest {
	overflow: hidden;
	width: 100%;

	.content-section {
		padding-bottom: 150px;
	}

	.video-info {
					color: #f62425;
					text-align: left;
					font-size: 1.5rem;
					line-height: 2.5rem;
					letter-spacing: 2px;
					font-family: $akzidenz;
					z-index: 15;
					position: absolute;
					top: 10%;
					left: 7%;

					span {
						overflow: hidden;
						display: block;
						height: 25px;

						i {
							display: inline-block;
						}

						&:first-of-type {
							i {
								transition: 1s all cubic-bezier(.97,.12,.06,.88) 0s;
							}
						}

						&:last-of-type {
							i {
								transition: 1s all cubic-bezier(.97,.12,.06,.88) 0.1s;
							}
						}
					}

					&.hidden {
						span {
							i {
								opacity: 0;
								visibility: hidden;
								transform: translate3d(0, 100%, 0);
							}

							&:first-of-type {
								i {
									transition: 0.6s all cubic-bezier(.97,.12,.06,.88) 1.1s;
								}
							}

							&:last-of-type {
								i {
									transition: 0.6s all cubic-bezier(.97,.12,.06,.88) 1s;
								}
							}
						}
					}

					.video-director {
						display: block;
					}

					a {
						color: #f62425;
						position: relative;

						&::after {
							content: '';
							position: absolute;
							left: -1px;
							bottom: -1px;
							display: block;
							height: 1px;
							width: 100%;
							background: #f62425;
						}
					}
				}


	.icon--press-play { display: none; }

	&.slideshow-animated {
		.director__name {
			&:first-of-type {
				opacity: 0 !important;
			}
		}
	}

	.title-wrapper {
		z-index: 10;
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 135px;
		margin-top: -45px;
	}

	&__name {
		user-select: none;
		color: #f62425;
		line-height: 1;
		font-size: 9rem;
		line-height: 9rem;
		text-align: center;
		letter-spacing: 20px;
		pointer-events: none;
		text-transform: uppercase;
		z-index: 10;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		transition: opacity 0.2s ease;

		&.animate {
			transition: none;
		}

		&--opacity {
			z-index: 0;
			opacity: 0.1;
		}
	}

	.background__cover__black {
		opacity: 0;
		position: absolute;
		top: 0; left: 0;
		width: 100%;
		height: 100%;
		background: $black;
	}

	&__cover {
		overflow: hidden;
		position: relative;
		height: 100vh;
		width: 100%;

		&__background {
			width: 100%;
			height: 100%;
		}

		&__title {
			font-size: 1.3rem;
			text-align: center;
			letter-spacing: 8px;
			font-family: $akzidenz;
			text-transform: uppercase;
			position: absolute;
			width: 100%;
			bottom: 0;
			color: #f62425;
		}

		&__scroll {
			text-align: center;
			position: absolute;
			bottom: 60px;
			width: 100%;

			.icon--arrow {
				opacity: 0;
				display: inline-block;
				margin-bottom: 10px;
				transform: scaleY(5);
				transform-origin: bottom center;

				&.is-animated {
					position: relative;
					animation: scrollArrowTransform 1s infinite linear;

					body.ie & {
						// z-index: -1;
						// animation: none;
					}
				}
			}

			i {
				opacity: 0;
				display: block;
				color: rgba(246,36,37,0.65);
			}
		}

		@keyframes scrollArrowTransform {
			25% {
				transform: translate3d(0, 10%, 0);
			}

			75% {
				transform: translate3d(0, -10%, 0);
			}
		}
	}

	.text-wrapper {
		cursor: pointer;
		font-size: 1.3rem;
		text-align: center;
		letter-spacing: 8px;
		font-family: $akzidenz;
		text-transform: uppercase;
		position: absolute;
		width: 100%;
		top: 53%;
		transform: translateY(-50%);
		transition: all 0.2s ease;

		&.hidden {
			opacity: 0;
			visibility: hidden;
		}

		.title {
			color: #f62425;
			line-height: 1;
			font-size: 5.5rem;
			line-height: 6.5rem;
			letter-spacing: 11px;
			text-align: center;
			font-family: $haptik;
			text-transform: uppercase;
			pointer-events: none;
			margin-bottom: 25px;
			padding: 0 40px;
		}

		.info {
			display: block;
			overflow: hidden;
			height: 15px;

			i {
				opacity: 0.6;
				color: #f62425;
				font-size: 1.5rem;
				line-height: 1.5rem;
				letter-spacing: 8px;
				text-transform: uppercase;
				font-family: $akzidenz;
				display: block;
				transform-origin: center;

				&.director {
					transition: opacity 0.4s linear 0.35s, transform 0.5s $easeOutQuint 0s;
				}

				&.hover-watch {
					opacity: 1;
					transition: transform 0.3s $easeOutQuint 0s;
				}
			}
		}

		&.no-video {
			&:hover {
				.info {
					i {
						opacity: 0.6;

						&.director, &.hover-watch {
							transform: none;
							transition: none;
						}
					}
				}
			}
		}

		&:hover, &.clicked {
			.info {
				i {
					opacity: 1;

					&.director {
						transform: translateY(-100%);
						transition: opacity 0.3s linear 0s, transform 0.3s $easeOutQuint;
					}

					&.hover-watch {
						transform: translateY(-100%);
						transition: transform 0.5s $easeOutQuint 0s;
					}
				}
			}
		}
	}

	&__realisations {
		width: span(14);
		margin: 0 auto;

		.single-realisation {
			text-align: center;
			overflow: hidden;
			position: relative;
			margin-top: 100px;

			&__video {
				position: relative;
				height: 0;
				padding-bottom: 56.25%;

				.video {
					position: relative;
					height: 0;
					padding-bottom: 56.25%;

					&-wrapper {
						position: absolute;
						top: 0; left: 0;
						width: 100%;
						height: 100%;

						&__overlay {
							z-index: 8;
							position: absolute;
							top: 0; left: 0;
							width: 100%;
							height: 100%;
							transition: all 0.2s ease;

							&.hidden {
								opacity: 0;
								visibility: hidden;
							}
						}

						&.hidden {
							.video-wrapper__overlay {
								opacity: 1;
								visibility: visible;
							}

							.controls {
								opacity: 0;
								visibility: hidden;
							}
						}

						video {
							position: relative;
							display: block;
							width: 100%;
							height: 100%;
						}

						iframe {
							&.quality-406 {
								position: absolute;
								top: 50%;
								left: 50%;
								transform: translate(-50%, -50%);
							}

							&.quality-720, &.quality-1080 {
								position: relative;
								display: block;
								width: 100%;
								height: 100%;
							}
						}

						.canvas-loader {
							z-index: 10;
							position: absolute;
							display: block;
							height: 20px;
							width: 20px;
							bottom: 60px;
							left: 0px;
							right: 0px;
							margin: 0 auto;
						}
					}
				}
			}

			&__slideshow {
				z-index: 1;
				text-align: center;
				position: relative;
				width: 100%;
				margin: 150px 0 0;

				.slideshow--title {
					z-index: 5;
					top: 48.5%;
				}

				.slideshow-wrapper {
					overflow: hidden;
				}

				.slideshow-container {
					font-size: 0;
					user-select: none;
					white-space: nowrap;
					width: 100%;
					height: 600px;
					height: 47vh;

					.block--slideshow {
						overflow: hidden;
						display: inline-block;
						vertical-align: top;
						height: 100%;
						margin-right: span(1 of 14);

						img {
							width: auto;
							height: 100%;
						}
					}
				}

				&.animated {
					.slideshow--title {
						opacity: 0;
						pointer-events: none;
					}
				}

				.slideshow--nav {
					margin-top: 40px;

					span {
						user-select: none;
						font-size: 1.3rem;
						letter-spacing: 5px;
						font-family: $akzidenz;
						color: rgba(246, 36, 37, 0.5);
						display: inline-block;
					}

					.prev-btn, .next-btn {
						cursor: pointer;
						transition: color 0.3s ease;

						&:hover {
							color: rgba(246, 36, 37, 0.8);
						}
					}

					.prev-btn {
						margin-right: 10px;
					}

					.next-btn {
						margin-left: 10px;
					}
				}
			}

			&__link {
				font-size: 1.3rem;
				text-align: center;
				letter-spacing: 5px;
				color: rgba(246, 36, 37, 0.2);
				display: inline-block;
				margin-top: 40px;
				transition: color 0.3s ease, opacity 0.2s ease 0.5s;

				&:hover {
					color: rgba(246, 36, 37, 1);
				}
			}

			.text-wrapper {
				z-index: 15;
			}
		}
	}

	&__presentation {
		z-index: 3;
		position: relative;
		padding: 150px 40px 0 40px;

		.title {
			color: rgba(246,36,37,0.2);
			line-height: 1;
			font-size: 7rem;
			text-align: center;
			font-family: $haptik;
			letter-spacing: 20px;
			text-transform: uppercase;
			pointer-events: none;
			z-index: 0;
			position: absolute;
			top: 0; left: 0;
			width: 100%;
			margin-bottom: 25px;
		}

		&__description {
			color: #f62425;
			font-size: 6rem;
			line-height: 8rem;
			text-align: center;
			max-width: 83%;
			margin: 0 auto 85px auto;
		}

		&__details {
			font-size: 1.9rem;
			line-height: 3rem;
			text-align: center;
			max-width: 52%;
			margin: 0 auto;
			color: #f62425;

			br {
				content: '';
				display: block;
				margin-bottom: 30px;
			}
		}

		&__social {
			text-align: center;
			margin-top: 130px;

			a {
				display: inline-block;
				padding: 10px;
				margin-right: 150px;
				transition: opacity 0.2s ease;

				&:last-child {
					margin-right: 0;
				}


				&:hover {
					opacity: 0.7;

					.text {
						opacity: 1;

						&::after {
							transform: scaleX(1);
							transform-origin: center left;
						}
					}
				}

				.text {
					color: #f62425;
					font-size: 1.4rem;
					line-height: 1.4rem;
					letter-spacing: 2px;
					font-family: $akzidenz;
					text-transform: lowercase;
					position: relative;

					&::after {
						content: '';
						position: absolute;
						left: -2px; right: 0;
						bottom: -2px;
						width: 98%;
						height: 1px;
						margin: auto;
						background: rgba(246,36,37,1);
						transform: scaleX(0);
						transform-origin: center right;
						transition: transform 0.2s $easeOutQuad;
					}
				}
			}
		}
	}

	#more-details {
		z-index: 150;
		display: none;
		position: relative;
		top: 0; left: 0;
		width: 100%;
		background: #141414;
	}
}

// MOBILE
body.mobile, body.tablet {
	.latest {
		.text-wrapper {
			pointer-events: none;
		}
	}

	.latest__cover__background {
		height: 100%;
	}

	.latest__realisations .single-realisation__video .video-wrapper {
		&.is-playing {
			top: 50%;
			height: 0;
			padding-bottom: 56.25%;
			transform: translateY(-50%);

			video, iframe {
				position: absolute;
				top: 0; left: 0;
			}
		}
	}

	.icon--press-play {
		z-index: 1;
		position: absolute;
		top: 0; bottom: 0;
		left: 0; right: 0;
		display: block;
		margin: auto;
		background-position: 0 100%;
		width: 75px;
		height: 75px;
		transition: all 0.2s ease;

		&.hidden {
			opacity: 0;
		}
	}
}

