		.text-wrapper {
			.title {
				font-size: 4.5rem;
				line-height: 5.5rem;
				letter-spacing: 9px;
			}
		}
	}
}

@media screen and (max-width: 1080px) {
	.latest {
		.title-wrapper {
			margin-top: -30px;
		}
	}

	.latest__name {
		font-size: 6rem;
		line-height: 6rem;
		letter-spacing: 13px;
	}

