			}
		}

		&__realisations .single-realisation__video .video-wrapper .canvas-loader {
			width: 15px;
			height: 15px;
			bottom: 20px;
		}
	}
}

@media screen and (max-width: 840px) {
	.latest {
		.text-wrapper {
			.title {
				font-size: 3rem;
				line-height: 4rem;
				letter-spacing: 6px;
			}
		}
	}
}

@media screen and (max-width: 770px) {
	.latest {
		&__realisations {
			width: 100%;
			padding: 0 40px;
			box-sizing: border-box;

			.single-realisation {
				margin-top: 55px;
			}

			.single-realisation__link {
