				margin-bottom: 50px;
			}

			&__details {
				color: #8f8f8f;
				font-size: 1.7rem;
				line-height: 2.7rem;
				letter-spacing: normal;
				font-family: $haptik;
				max-width: 100%;

				br { margin-bottom: 25px; }
			}
		}
	}
}

@media screen and (max-width: 600px) {
	.latest__cover__scroll {
		bottom: 10px;

		i { display: none; }
	}

	.latest {
		.content-section {
			padding-bottom: 100px;
			padding-top: 60px;
		}

		.text-wrapper {
			.info {
				height: auto;
				padding: 0 40px;
				box-sizing: border-box;

				i {
					opacity: 1;

					&.hover-watch { display: none; }
				}
			}
		}

		.title-wrapper {
			height: 50px;
			margin-top: -12.5px;
		}

		.latest__cover__title {
			font-size: 0.75rem;
			line-height: 0.75rem;
		}

		&__name {
			font-size: 2.5rem;
			line-height: 2.5rem;
			letter-spacing: 4px;
		}

		&__realisations {
			padding: 0 20px;
			margin-top: 20px;

			&:first-of-type {
				margin-top: 45px;
			}

			.single-realisation {
				&__link	{
					font-size: 0.85rem;
					margin-top: 10px;
				}

				&__slideshow {
					margin-top: 45px;
				}

				&__video .video-wrapper .canvas-loader {
					bottom: 15px;
				}

				&__description {
					font-size: 2rem;
					line-height: 2.5rem;
					margin-bottom: 25px;
				}
			}
		}

		&__presentation {
			&__details {
				font-size: 1.25rem;
				line-height: 2rem;

				br { margin-bottom: 15px; }
			}

			&__social {
				margin-top: 50px;

				a {
					margin-right: 4%;
				}
			}
		}
	}

	#latest nav ul li a {
		$scale: 1.6 / 2.5;
		letter-spacing: 4px;
		transform: scale($scale);
	}
}

@media screen and (max-width: 555px) {
	.latest {
		.text-wrapper {
			.title {
				font-size: 2rem;
				line-height: 3rem;
				letter-spacing: 4px;
				margin-bottom: 5px;
			}

			.info {
				padding: 0 20px;

				i {
					opacity: 1;
					font-size: 0.75rem;
					line-height: 1.65rem;
				}
			}
		}
	}

	#latest nav ul li a {
		$scale: 1.2 / 2.5;
		letter-spacing: 3.8px;
		transform: scale($scale);
	}
}

@media screen and (max-width: 500px) {
	.latest__presentation {
		padding-top: 65px;
	}
}

@media screen and (max-width: 450px) {
	.latest {
		.text-wrapper {
			.title {
				font-size: 1.5rem;
				line-height: 2rem;
				letter-spacing: 3px;
			}
		}
	}
}

@media screen and (max-width: 400px) {
	.latest__realisations .single-realisation__video .video-wrapper .canvas-loader {
		width: 10px;
		height: 10px;
	}

	.latest__presentation {
		padding-top: 45px;
	}
}

@media screen and (max-width: 385px) {
	.latest {
		.title-wrapper {
			margin-top: -10px;
		}
	}

	.latest__name {
		font-size: 2rem;
		line-height: 2rem;
		letter-spacing: 3px;
	}

	#latest nav ul li a {
		$scale: 1.2 / 2;
		letter-spacing: 3px;
		transform: scale($scale);
	}
}

@media screen and (max-width: 360px) {
	.latest {
		.text-wrapper {
			.title {
				font-size: 1.3rem;
				letter-spacing: 1.8px;
			}

			.info i {
				line-height: 1.2rem;
			}
		}

		&__cover__title {
			letter-spacing: 6px;
		}

		&__presentation {
			padding-top: 25px;

			&__social a {
				margin-right: 0;
			}
		}
	}
}