#not-found {
	height: 100vh;
	width: 100%;
	
	.container {
		display: table;
		text-align: center;
		height: 100%;
	}
	
	.center {
		display: table-cell;
		vertical-align: middle;
	}
	
	.head {
		color: #fff;
		font-size: 9rem;
		line-height: 11rem;
		letter-spacing: 6px;
		font-family: $haptik;
		text-transform: uppercase;
	}

	@supports (-webkit-background-clip: text) {
		
		.head {
			text-fill-color: transparent;
			-webkit-text-fill-color: transparent;

			-webkit-background-clip: text;
			background-clip: text;

			background-size: 250% auto;

			del {
				-webkit-text-fill-color: #111111;
				text-fill-color: #111111;
			}
		}

		.svg-filter { display: none; }

	}

	@supports not (-webkit-background-clip: text) {
		
		.head {
			color: #fff;
			text-fill-color: #fff;
			-webkit-text-fill-color: #fff;
			background: none;

			del {
				color: rgba(0, 0, 0, 0.05);
				background: none;
			}

			::-moz-selection {
				color: #fcdeb0; 
				background: none;			
			}
		}

	}

	p {
		color: #4b4b4b;
		font-size: 1.3rem;
		line-height: 3.5rem;
		letter-spacing: 8px;
		text-transform: uppercase;
		margin-top: 20px;
	}
}