#index {
	text-align: center;
	min-height: 100%;
	width: 100%;

	&.no-tap {
		.slideshow {
			-webkit-tap-highlight-color: rgba(0,0,0,0);
			-webkit-tap-highlight-color: transparent;
		}
	}

	.preload {
		z-index: 100;
		line-height: 100vh;
		text-align: center;
		position: fixed;
		top: 0; bottom: 0;
		left: 0; right: 0;
		/*background: $black;*/
		background-color: #fff;
		transform-origin: center top;
	}

	.loading {
		z-index: 110;
		position: absolute;
		top: 0;
		left: 0;
		height: 4px;
		width: 100%;
		background: #f62425;
		transform-origin: left center;
		transform: scaleX(0);
	}

	.grab-icon {
		opacity: 0;
		z-index: 20;
		pointer-events: none;
		position: absolute;
		bottom: 20%;
		left: 0; right: 0;
		display: block;
		width: 60px;
		height: 26px;
		margin: auto;

		&.is-animated {
			animation: grabAnimation 5s infinite cubic-bezier(0.22, 0.61, 0.36, 1);
		}

		@keyframes grabAnimation {
			15% {
				transform: translate3d(10%, 0, 0);
			}

			85% {
				transform: translate3d(10%, 0, 0);
			}

			100% {
				transform: translate3d(0%, 0, 0);
			}
		}
	}

	.click-watch {
		color: #f62425;
		font-size: 5rem;
		font-family: $haptik;
		line-height: 100vh;
		letter-spacing: 15px;
		text-transform: uppercase;
		z-index: 110;
		position: absolute;
		left: 0; right: 0;
		display: inline-block;
		vertical-align: middle;
		pointer-events: none;

		span {
			opacity: 0;
			display: inline-block;
			transform: translateY(25px);
		}

		i {
			font-size: 3.5rem;
			display: inline-block;
			vertical-align: top;
			margin-top: -8px;
		}

		&.minified {
			color: #f62425;
			font-size: 1.5rem;
			line-height: 1.5rem;
			letter-spacing: 3px;
			bottom: 50px;

			span {
				transform: translateY(20px);
			}

			i {
				font-size: 1.5rem;
				line-height: 1.5rem;
				margin-top: 0;
			}
		}
	}

	.slideshow-container {
		z-index: 15;
		position: absolute;
		top: 0; bottom: 0;
		left: 0;
		display: block;
		height: 100%;
		width: 100%;
	}

	.slideshow {
		font-size: 0;
		line-height: 100vh;
		white-space: nowrap;
		position: absolute;
		top: 0; bottom: 0;
		left: 50%;
		display: block;
		width: 100%;
		-webkit-tap-highlight-color: rgba(20,20,20,0.3);

		&__item {
			user-select: none;
			position: relative;
			display: inline-block;
			vertical-align: middle;
			width: span(12);
			max-width: 1280px;
			margin: 0 span(1) 0 0;
			transform: translate3d(0, 0, 0);

			&.pointer {
				cursor: pointer;
			}

			.text-wrapper {
				z-index: 5;
				color: #f62425;
				line-height: 1rem;
				text-align: center;
				cursor: pointer;
				user-select: none;
				position: absolute;
				top: 53%;
				left: 50%;
				display: block;
				width: 100%;
				margin: 0 auto;
				padding: 0 20px;
				box-sizing: border-box;
				transform: translate(-50%, -50%) scale(2);

				&.animated {
					transform: translate(-50%, -50%) scale(1);
					transition: opacity 0.1s ease;
				}

				&.hidden {
					opacity: 0;
					visibility: hidden;
				}

				h1 {
					color: #f62425;
					font-size: 6rem;
					line-height: 7rem;
					white-space: normal;
					letter-spacing: 13px;
					font-family: $haptik;
					text-transform: uppercase;
					margin-bottom: 25px;
					transition: color 0.4s ease;
				}

				.info {
					display: block;
					overflow: hidden;
					height: 13px;

					i {
						opacity: 0.6;
						color: #f62425;
						font-size: 1.3rem;
						line-height: 1.3rem;
						letter-spacing: 7px;
						text-transform: uppercase;
						font-family: $akzidenz;
						display: block;
						transform-origin: center;

						&.director {
							transition: opacity 0.4s linear 0.35s, transform 0.5s $easeOutQuint 0s;
						}

						&.hover-watch {
							opacity: 1;
							transition: transform 0.3s $easeOutQuint 0s;
						}
					}
				}

				&:hover, &.clicked {
					h1 {
						color: #f62425 !important;
					}

					i {
						opacity: 1;

						&.director {
							transform: translateY(-100%);
							transition: opacity 0.3s linear 0s, transform 0.3s $easeOutQuint;
						}

						&.hover-watch {
							transform: translateY(-100%);
							transition: transform 0.5s $easeOutQuint 0s;
						}
					}
				}
			}

			.video-preview-wrapper {
				width: 100%;
				height: 0;
				background: #131313;
				padding-bottom: 56.25%;

				video {
					outline: none;
					position: absolute;
					top: 0; left: 0;
					height: 100%;
					width: 100%;
					transform: translate3d(0, 0, 0);
				}

				canvas {
					user-select: none;
					position: absolute;
					bottom: 60px;
					left: 0; right: 0;
					display: block;
					height: 20px;
					width: 20px;
					margin: 0 auto;
				}
			}

			.video-full-wrapper {
				z-index: 10;
				position: absolute;
				top: 0; bottom: 0;
				left: 0; right: 0;
				display: block;
				width: 100%;
				height: 100%;
				transition: all 0.1s ease;

				&.hidden {
					opacity: 0;
					visibility: hidden;
				}

				.mobile-cover, .icon--press-play { display: none; }

				.video-info {
					color: #f62425;
					text-align: left;
					font-size: 1.5rem;
					line-height: 2.5rem;
					letter-spacing: 2px;
					font-family: $akzidenz;
					z-index: 15;
					position: absolute;
					top: 10%;
					left: 7%;

					span {
						overflow: hidden;
						display: block;
						height: 25px;

						i {
							display: inline-block;
						}

						&:first-of-type {
							i {
								transition: 1s all cubic-bezier(.97,.12,.06,.88) 0s;
							}
						}

						&:last-of-type {
							i {
								transition: 1s all cubic-bezier(.97,.12,.06,.88) 0.1s;
							}
						}
					}

					&.hidden {
						span {
							i {
								opacity: 0;
								visibility: hidden;
								transform: translate3d(0, 100%, 0);
							}

							&:first-of-type {
								i {
									transition: 0.6s all cubic-bezier(.97,.12,.06,.88) 1.1s;
								}
							}

							&:last-of-type {
								i {
									transition: 0.6s all cubic-bezier(.97,.12,.06,.88) 1s;
								}
							}
						}
					}

					.video-director {
						display: block;
					}

					a {
						color: #f62425;
						position: relative;

						&::after {
							content: '';
							position: absolute;
							left: -1px;
							bottom: -1px;
							display: block;
							height: 1px;
							width: 100%;
							background: #f62425;
						}
					}
				}

				video, object, iframe {
					position: absolute;
					top: 0; bottom: 0;
					left: 0; right: 0;
					display: block;
					height: auto;
					width: 100%;
					margin: auto;
				}

				iframe {
					height: 100%;
					background: #141414;

					&:not(.is-fullscreen).quality-406 {
						position: absolute;
						top: 0;
						left: 0;
						transform: translate(0%, 0%) !important;
					}
				}
			}
		}
	}

	.additional-content {
		user-select: none;
		position: absolute;
		display: block;
		width: 100%;

		.movies {
			width: 80%;
			margin: auto;

			&__item {
				will-change: margin;
				overflow: hidden;
				text-align: center;
				position: relative;
				display: block;
				height: 0;
				width: 100%;
				padding-bottom: 56.25%;
				background: #000;

				.text-wrapper {
					z-index: 15;

					h1 {
						font-size: 5.2rem;
						line-height: 6.2rem;
						letter-spacing: 14px;
					}

					.info {
						height: 10px;

						i {
							font-size: 1.1rem;
							line-height: 1.1rem;
						}
					}
				}

				.poster {
					z-index: 5;
					position: relative;
					height: 100%;
					width: 100%;
					transition: all 0.3s ease;

					&.hidden {
						opacity: 0;
						visibility: hidden;
					}
				}

				.canvas-loader {
					z-index: 5;
					user-select: none;
					position: absolute;
					left: 0; right: 0;
					bottom: 60px;
					display: block;
					height: 20px;
					width: 20px;
					margin: 0 auto;
				}

				&--director {
					a {
						position: absolute;
						top: 0; left: 0;
						width: 100%;
						height: 100%;

						&:hover {
							.text-wrapper .info {
								i {
									opacity: 1;

									&.director {
										transform: translateY(-100%);
										transition: opacity 0.3s linear 0s, transform 0.3s $easeOutQuint;
									}

									&.hover-watch {
										transform: translateY(-100%);
										transition: transform 0.5s $easeOutQuint 0s;
									}
								}
							}
						}
					}

					.text-wrapper {
						h1 {
							font-size: 5.5rem;
							line-height: 5.5rem;
							letter-spacing: 12px;
						}
					}
				}
			}
		}
	}

	.prev-btn, .next-btn {
		opacity: 0;
		z-index: 20;
		cursor: pointer;
		position: absolute;
		top: 0;
		height: 100%;
		width: span(2 wide);
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
		filter: alpha(opacity=0);
		transition: opacity 0.3s ease;

		&.visible {
			opacity: 1;
			-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=1)";
			filter: alpha(opacity=1);
		}

		span {
			opacity: 1;
			color: rgba(246,36,37,1);
			font-size: 1.4rem;
			line-height: 1.4rem;
			letter-spacing: 2px;
			position: absolute;
			top: 50%;
			left: 0; right: 0;
			width: 100%;
			margin-top: -7px;
		}
	}

	.prev-btn {
		left: 0;
	}

	.next-btn {
		right: 0;
	}

	#close, #view-more {
		opacity: 1;
		z-index: 20;
		cursor: pointer;
		color: #f62425;
		font-size: 1.5rem;
		letter-spacing: 3px;
		font-family: $akzidenz;
		visibility: visible;
		position: absolute;
		bottom: 60px;
		left: 50%;
		padding: 15px;
		margin: 0;
		outline: none;
		border: none;
		background: none;
		transform: translateX(-50%);
		transition: color 0.2s ease, opacity 0.2s ease 0.5s;

		&.hidden {
			opacity: 0;
			visibility: hidden;
		}

		&::after {
			content: '';
			position: absolute;
			left: -4px; right: 0;
			bottom: 12px;
			width: 83%;
			height: 1px;
			margin: auto;
			background: rgba(246,36,37,0.6);
			transform: scaleX(0);
			transform-origin: center right;
			transition: transform 0.2s $easeOutQuad;
		}

		&:hover {
			color: #f62425;

			&::after {
				transform: scaleX(1);
				transform-origin: center left;
			}
		}
	}

	#close {
		opacity: 0;
		color: #f62425;
		visibility: hidden;

		&.animate { transform: translateX(-50%); transition: none; }

		&::after { width: 60%; }
	}

	.canvas-loader-mobile { display: none; }
}

