// MOBILE
body.mobile, body.tablet {
	#index {
		overflow: hidden;

		.preload {
			background: none;

			svg {
				position: absolute;
				top: 0; bottom: 0;
				left: 0; right: 0;
				margin: auto;
			}
		}

		.click-watch {
			color: rgba(255,255,255,0.5);
			font-size: 1rem;
			line-height: 1rem;
			letter-spacing: 8px;
			bottom: 25px;
			transition: opacity 0.4s ease;

			&.hidden {
				opacity: 0;
				visibility: hidden;
			}

			span {
				opacity: 1;
				transform: translateY(0px);
			}

			i {
				font-size: 1rem;
				vertical-align: middle;
				margin-top: 0;
			}
		}

		.slideshow {
			line-height: 1;
			overflow: hidden;
			left: 0; right: 0;

			&__item {
				overflow: hidden;
				height: 100%;
				width: 100%;
				margin: 0;

				.text-wrapper {
					z-index: 15;
					pointer-events: none;
					transform: translate3d(-50%, -50%, 0);

					h1 {
						margin-bottom: 35px;
					}

					.info {
						overflow: visible;
						height: auto;

						i {
							font-size: 1.5rem;
							line-height: 1.5rem;
							font-family: $akzidenz;

							&.hover-watch { display: none; }
						}
					}

					&:hover, &:active, &.clicked {
						h1 {
							color: #fff !important;
						}

						i {
							&.director, &.hover-watch {
								transform: none;
							}
						}
					}
				}

				.video-preview-wrapper { display: none; }

				.video-full-wrapper {
					z-index: 10;

					&.is-playing {
						height: 0;
						padding-bottom: 56.25%;
						top: 50%;
						transform: translateY(-50%);

						.controls { bottom: -55px; }

						@media (orientation: landscape) {
							.controls { top: none; bottom: 0; }
						}
					}

					video, iframe {
						z-index: -1;
					}

					.mobile-cover {
						z-index: 6;
						position: absolute;
						top: 0; left: 0;
						pointer-events: none;
						display: block;
						height: 100%;
						width: 100%;
						transition: opacity 0.2s ease;

						&.hidden {
							opacity: 0;
							visibility: hidden;
						}
					}

					.icon--press-play {
						z-index: 1;
						position: absolute;
						top: 0; bottom: 0;
						left: 0; right: 0;
						display: block;
						margin: auto;
						background-position: 0 100%;
						width: 75px;
						height: 75px;
						transition: all 0.2s ease;

						&.hidden {
							opacity: 0;
						}
					}
				}
			}
		}

		.mobile-nav {
			z-index: 55;
			position: absolute;
			left: 0; right: 0;
			bottom: 3.5%;
			display: block;
			margin: auto;
			transition: opacity 0.3s ease;

			&.hidden {
				opacity: 0;
				visibility: hidden;
			}

			div, span {
				font-size: 1.2rem;
				text-align: center;
				letter-spacing: 5px;
				font-family: $akzidenz;
				color: rgba(255,255,255,0.7);
				display: inline-block;
				vertical-align: middle;
			}

			.prev-btn-mobile, .next-btn-mobile {
				opacity: 1;
				position: static;
				padding: 15px;
				width: auto;
				background: none;
			}

			span {
				color: rgba(255,255,255,0.6);
				margin: 0 5px;
			}
		}

		#view-more, #close, .prev-btn, .next-btn, .additional-content { display: none; }

		.canvas-loader-mobile {
			z-index: 15;
			user-select: none;
			position: absolute;
			bottom: 60px;
			left: 0; right: 0;
			display: block;
			height: 15px;
			width: 15px;
			margin: auto;
		}
	}

	@media screen and (max-width: 680px) {
		#index {
			.slideshow {
				&__item {
					.text-wrapper {
						h1 {
							margin-bottom: 20px;
						}

						.info {
							i {
								font-size: 1.1rem;
								line-height: 1.1rem;
							}
						}
					}
				}
			}
		}
	}

	@media screen and (max-width: 640px) {
		#index {
			.preload {
				svg {
					width: 175px;
					height: 29px;
				}
			}
		}
	}

	@media screen and (max-width: 555px) {
		#index {
			.click-watch {
				letter-spacing: 8px;

				span, i {
					font-size: 0.8rem;
					line-height: 0.8rem;
					letter-spacing: 6px;
				}
			}

			.slideshow {
				&__item {
					.text-wrapper {
						padding: 0 10px;

						h1 {
							margin-bottom: 10px;
						}

						.info {
							i {
								font-size: 0.75rem;
								line-height: 0.75rem;
								letter-spacing: 3px;
							}
						}
					}
				}
			}
		}
	}

	@media screen and (max-width: 500px) {
		#index {
			.grab-icon {
				width: 40px;
				svg {
					width: 40px;
				}
			}

			.click-watch {
				letter-spacing: 8px;

				span, i {
					font-size: 0.8rem;
					line-height: 0.8rem;
					letter-spacing: 6px;
				}
			}
		}
	}

	@media screen and (max-width: 400px) {
		#index {
			.click-watch {
				letter-spacing: 8px;

				span, i {
					font-size: 0.6rem;
					line-height: 0.6rem;
					letter-spacing: 5px;
				}
			}
		}
	}

	@media screen and (max-width: 355px) {
		#index {
			.slideshow__item .text-wrapper .info i {
				font-size: 0.7rem;
				line-height: 0.7rem;
				letter-spacing: 2px;
			}
		}
	}
}


// TABLET
body.tablet {
	#index .slideshow__item .video-full-wrapper {
		&.mp4, &.webm {
			&.is-playing {
				@media (orientation: landscape) {
					top: 53%;
				}

				.controls {
					transform: translateY(-100%);
				}
			}
		}

		video {
			width: 80% !important;
			height: auto !important;
		}
	}

	#index .slideshow__item .video-full-wrapper {
		&.wiredrive {
			&.is-playing {
				@media (orientation: landscape) {
					top: 53%;
				}
			}
		}

		iframe {
			width: 80% !important;
		}
	}
}